@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
#search {
    margin: auto;
    margin-top: 10px;
}

#search input[type=submit] {
    margin-left: 5px;
    font-size: 16px;
    padding: 5px 10px;
    background-color: rgb(114, 235, 215);
    border-radius: 10px;
    border: 1px solid gray;
}

#options-group {
    display: flex;
    justify-content: space-evenly;
    text-align: left;
}

.login-signup-button {
    margin-top: 25px;
    font-size: 20px;
    padding: 10px 20px;
    background-color: rgb(214, 253, 234);
    border-radius: 10px;
    border: 1px solid gray;
}

.login-signup-link {
    color: rgb(2, 88, 48);
}
main {
    width: 800px;
    margin: 30px;
    margin: auto;
    text-align: center;

    border: 1px solid black;
    border-radius: 15px;
    background-color: rgb(42, 153, 125);
    padding: 30px;
}

#slash-title {
    margin: 30px 0;
    font-size: 80px;
    font-family: 'Bebas Neue', cursive;
    text-align: center;
    margin-left: 500px;
}

#top-right-links {
    float: right;
    display: flex;
    justify-content: right;
    width: 500px;
    font-size: 20px;
}

#top-right-links a {
    margin-right: 50px;
}

html, body {
    background-color: #777b77;
    width: 100%;
    /* height: 100%; */
    margin: 0;
    font-family: sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    opacity: 0.6;
}

input[type=submit]:hover {
    opacity: 0.8;
    cursor: pointer;
}

hr {
    border: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
}

.icon-text-container {
    position: relative;
    display: inline;
}

.icon-text-container input[type=text], .icon-text-container input[type=password] {
    padding: 6px;
    border-radius: 10px;
    border: none;
    box-sizing: border-box;
    text-indent: 25px;
    font-size: 16px;
}

.text-icon {
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translateY(-50%);
    text-align: center;
    color: #888;
    font-size: 15px;
}

.item-table {
    text-align: left;
    width: 100%;
    border-collapse: collapse;
}

.item-table th, .item-table td {
    padding: 8px;
}

.item-table th {
    border-bottom: 1px solid black;
}

.item-table td {
    border-top: 1px solid black;
}

.name-td {
    width: 360px;
}

.option {
    margin: 20px;
}

.option h4 {
    margin-top: 0;
}

.item-img-container {
    width: 80px;
    height: 80px;
    position: relative;
    display: inline-block;
}

.item-img-container img {
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

